import { Col, Container, Row } from "react-bootstrap";
import DashboardLeft from "../../components/dashboard-left";
import Search from "../../components/seach-box";
import "./index.scss";
import ProjectsProgress from "../../components/dashboard-right/projects-progress";
import RecentConnect from "../../components/dashboard-right/recent-connect";
import RecentMessage from "../../components/dashboard-right/recent-message";

function DashBoard() {
  const list = [];
  for (let i = 0; i < 8; i++) {
    list.push({
      title: "Title " + i,
      author: "author " + i,
      status: "Active",
      date: new Date().toLocaleString(),
    });
  }

  const recentMessages = [
    {
      name: "Black",
      date: new Date().toDateString(),
      content: "Lorem ipsum dolor sit amet",
    },
    {
      name: "WWhite",
      date: new Date().toDateString(),
      content: "Lorem ipsum dolor sit amet",
    },
  ];

  //
  const recentConnects = [
    {
      name: "Black",
      content: "Lorem ipsum dolor sit amet",
    },
    {
      name: "WWhite",
      content: "Lorem ipsum dolor sit amet",
    },
  ];
  return (
    <Container>
      <div className="dasboard">
        <Search showButton={true} />
        <Row>
          <Col md={7}>
            <DashboardLeft list={list} />
          </Col>
          <Col md={5}>
            <div className="dashboard-right">
              <RecentMessage recentMessages={recentMessages} />
              <RecentConnect recentConnects={recentConnects} />
              <ProjectsProgress />
            </div>
          </Col>
        </Row>
      </div>
    </Container>
  );
}

export default DashBoard;
